import React, { useState, useEffect } from 'react'
import Card from '../components/Card'
import axios from 'axios'

const Home = () => {
  const [apps, setApps] = useState(null)
  const [tagset, setTags] = useState(null)
  const [filters, setFilters] = useState([])
  
  const fetchData = async () => {
    const results = await axios.get('/.netlify/functions/getCategory?category=apps')
    setApps(Object.values(results.data[1]))
  }

  const getTags = async () => {
    const results = await axios.get('/.netlify/functions/getTags')
    setTags(Object.values(results.data))
  }

  useEffect(() => {
    fetchData()
    getTags()
    setFilters()
  }, [])

  console.log(JSON.stringify(tagset))

  console.log(JSON.stringify(apps))

  const onClick = (tagname) => {
    console.log(filters)
    let tagindex = -1
    if ((filters && filters.length) > 0) {
      tagindex = filters.indexOf(tagname)
    }
     if (tagindex !== -1) {
      setFilters( arr => [...arr, tagname]);
    } else {
      if (filters) {
        delete filters[tagindex]
      }
    }
  };

  return (
    <>
    {apps && tagset && (
      <div className='container'>
        <div className="row">
      {tagset && (
        <div className='container'>
          <div class="btn-group-vertical">
            {tagset.map((tag, index) => (
                <button key={index} className="hold" onClick={onClick(tag.name)}>{tag.name}</button>
            ))}
          </div>
        </div>
      )}
      
          
        </div>
        <div className="row row-cols-3">
          <div className='col'></div>
      
          {apps.map((application, index) => (
            <Card
              key={index}
              application={application}
            />
          ))}
        </div>
      </div>
    )}
    </>
  )
}

export default Home